@import '../../../sassStyles/_mixins';

.section {
	position: relative;
	width: 100%;
	height: 100%;
	padding: 0 8%;
}
.section__inner { // wrapper for animation (show section after preloader) because .section hide animation comes from react-transition-group
	width: 100%;
	height: 100%;
	opacity: 0; //
	animation: showSection 0.4s; //
	animation-fill-mode: forwards; //
}



@keyframes showSection {
	0% {
		opacity: 0;
		transform: scale(.8);
	}
	100% {
		opacity: 1;
		transform: scale(1);
	}
}